div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  background-color: #ddd;
  color: #222;
}
header{
  flex-basis: 30vh;

}

main{
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}
section.page{
  flex-grow: 1;
  background-color: rgb(137, 144, 120);
}


aside{
  min-width: 240px;
  background-color: #999;
}

footer{
background-color: #000;
color: white;
padding: 10px 0;
text-align: center;
text-transform: uppercase;
margin-bottom: 50px;
}